@import url(https://fonts.googleapis.com/css2?family=M+PLUS+2:wght@300;400;600;700&family=Source+Code+Pro:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #000;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.albumChoice{
    position: absolute;
    top: 295px;
    display: flex;
    flex-direction: column;
    font-family: 'Source Code Pro', monospace;
    font-size: 14px;
}

.area{
    text-align: center;
    background-color: #bababa;
    border-radius: 20px 0 0 20px;
}

.previewImg{    
    margin: 10px;
    /* box-shadow: .5px .5px .5px .5px rgba(0, 0, 0, .2); */
    transition: all 0.15s ease-in-out;
}

.previewImg::after {
    opacity: 0;
    transition: opacity 0.05s ease-in-out;
}

.previewImg:hover{
    transform: scale(1.1, 1.1);
    box-shadow: 3px 3px 3px 3px rgba(0,0,0,0.2);
}

.previewImg::after{
    opacity: 1;
}
.selectedAl {
    border: 4px solid #000;
}

.selectedAl-enter {
    opacity: 0;
}

.selectedAl-enter-active {
    opacity: 1;
    transition: opacity 6000ms;
}

.selectedAl-exit {
    opacity: 1;
}

.selectedAl-exit-active {
    opacity: 0;
    transition: opacity 6000ms;
}
.wrapper{
    position: absolute;
    z-index: -1;
    font-size: 15px;
    font-family: 'Source Code Pro';
    line-height: 22px;
    color: white;
    padding-left: 6px;
    opacity: 10%;
    word-break: break-all;
    position: fixed;
}
audio::-webkit-media-controls-panel{
    background-color: #7d7d7d;
}

audio::-webkit-media-controls-current-time-display, audio::-webkit-media-controls-time-remaining-display{
    color: #f5f5f5;
    outline: 0;
}

audio::-webkit-media-controls-timeline{
    color: #f5f5f5;
}

.audioPlayer{
    width: 45%;
}

.audioArea{
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 14px;
    padding: 4%;
}
.wee{
    width: 1100px;
    margin: 0 auto;
    position: relative;
}

.main {
    margin: auto;
    margin-top: 2px;
    margin-bottom: 20px;
    margin-left: 10%;
    width: 900px;
    height: 900px;
    background-color: #bababa;
    border-radius: 80px;
    font-family: 'Source Code Pro', monospace;
    font-weight: 500;
    white-space: pre-wrap;
}

.musicCol{
    padding-top: 2%;
    display: flex;
    justify-content: space-evenly;
    align-items: flex-end;
    font-size: 14px;
}

.descBox{
    border: 2.5mm ridge rgba(82, 82, 82, 0.6);
    width: 40%;
    height: 430px;
    overflow: scroll;
    overflow-x: hidden;
    /* background aphex logo */
    background: no-repeat url(/project2repo/static/media/logo.3217e469.png) center;
    background-size: 600px;
    background-color: #dedede;
    /* text attributes */
    font-size: 14px;
    text-align: left;
    padding-left: 4px;
    word-wrap: break-word;
}

.actualHeader{
    border-radius: 80px;
    font-family: 'conthrax';
    /* background aphex banner */
    background: no-repeat url(/project2repo/static/media/banner.7fac8338.png) top;
    background-size: 700px;
    background-color: #dbdbdb;
}

.bannerArea{
    padding: 20px 0px;
    position: relative;
    right: 40px;
    display: inline-grid;
    grid-template-columns: 1fr 700px;
}

h1{
    font-size: 30px;
}

.generalInfo{
    margin-left: 10px;
    padding-left: 15px;
    border: .1px solid #969696; 
    border-radius: 20px;
    border-right: 0;
    border-bottom: 0;
}

@font-face {
    font-family: nk-monospace;
    src: url(/project2repo/static/media/nk57-monospace-se-bd.5199860d.otf);
}

@font-face {
    font-family: conthrax;
    src: url(/project2repo/static/media/conthrax-sb.787c73c4.otf);
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #888;
}

::-webkit-scrollbar-thumb:hover {
  background: #666;
}
